<template>
    <div>
        <loading :active.sync="isLoading" ></loading>
        <Cart class="sticky-top" :cartList="cartData" :cartLength="cartLength" @removeCart="removeCart"></Cart>
        <div>
            <div class="row mx-0">
                <div class="col-lg-2 ">
                    <nav class="nav flex-lg-column pt-3 pl-1 h-100 justify-content-lg-start justify-content-center">
                        <a class="nav-link hover-link text-secondary " @click.prevent="assort('all')" :class="{'sidebar-active':clickCategory ==='all'}" href="#">
                            <i class="fas fa-rainbow mr-2 text-info"></i>多樣的空氣</a>
                        <a class="nav-link hover-link text-secondary" @click.prevent="assort('山')" :class="{'sidebar-active':clickCategory ==='山'}" href="#">
                            <i class="fas fa-tree text-success mr-2"></i>山中的空氣</a>
                        <a class="nav-link hover-link text-secondary" @click.prevent="assort('海')" :class="{'sidebar-active':clickCategory ==='海'}" href="#">
                            <i class="fas fa-water mr-2 text-primary"></i>海中的空氣</a>
                        <a class="nav-link hover-link text-secondary" @click.prevent="assort('田野')" :class="{'sidebar-active':clickCategory ==='田野'}" href="#">
                            <i class="fas fa-seedling text-warning mr-2"></i>田野的空氣</a>
                    </nav>
                </div>
                <div class="col-lg-10" v-if="!detail">
                    <div class="row mt-2">
                        <div class="col-md-4 mb-4" v-for="item in products" :key="item.id" v-show="clickCategory === item.category || clickCategory === 'all'">
                            <div class="card border-0 shadow">
                                <div style="height: 200px; background-size: cover; background-position: center"
                                :style="{backgroundImage:`url(${item.imageUrl})`}">
                                </div>
                                <div class="card-body pb-0 mt-2">
                                    <span class="badge badge-info float-right ml-2">{{item.category}}</span>
                                    <h5 class="card-title">
                                        <a href="#" @click.prevent="productDetail(item)" class="text-secondary">{{item.title}}</a>
                                    </h5>
                                    <div class="text-right mt-5">
                                        <div class="h5 py-2" v-if="!item.price">{{item.origin_price | currency}}</div>
                                        <del class="h6 py-2 text-muted" v-if="item.price">{{item.origin_price | currency}} </del>
                                        <div class="h5 py-2 text-danger">{{item.price | currency}}</div>
                                    </div>
                                </div>
                                <div class="card-footer d-flex bg-transparent border-0">
                                    <button type="button" class="btn btn-outline-secondary btn-sm"
                                    @click.prevent="productDetail(item)">
                                        <i class="fas fa-spinner fa-spin mr-2" v-if="status.fileLoading === item.id"></i>
                                        <i class="fas fa-chevron-circle-right mr-2" v-else></i>查看更多
                                    </button>
                                    <button @click.prevent="addProduct(item.id)" type="button" class="btn btn-desert ml-auto">
                                        <i class="fas fa-spinner fa-spin mr-2" v-if="status.cartLoading === item.id"></i>
                                        <i class="fas fa-cart-plus "></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <!-- Pagination -->
                    <Pagination :pagination="pagination" v-on:getPageProducts="getProducts"/>
                </div>
                <div class="col-lg-10 px-0 mt-3" v-if="detail">
                    <div class="row mx-0 py-3">
                        <div class="col-lg-8">
                            <div style="height: 400px; background-size: cover; background-position: center"
                            :style="{backgroundImage:`url(${product.imageUrl})`}">
                            </div>
                        </div>
                        <div class="col-lg-4 pt-5">
                            <h5 class="text-dark">{{product.title}}</h5>
                            <hr>
                            <blockquote class="py-3 text-muted">
                                <p>{{product.content}}</p>
                                <footer >{{product.description}}</footer>
                            </blockquote>
                            <div class="text-right pt-3">
                                <div class="h5" v-if="!product.price">價格{{product.origin_price | currency}}</div>
                                <div class="h5 text-secondary" v-if="product.price">價格 {{product.price | currency}} 元</div>
                            </div>
                            <div class="form-group">
                                <select class="form-control" id="itemSelect" v-model="product.num">
                                    <option :value="n" v-for="n in 10" :key="n">選購 {{n}} {{product.unit}}</option>
                                </select>
                            </div>
                            <div class="text-muted h6 mr-3 mb-0 text-nowrap text-right pt-2" v-if="product.num">
                            總計 <strong class="text-success" v-if="product.price">{{product.price * product.num | currency}} 元</strong>
                                <strong class="text-success" v-if="!product.price">{{product.origin_price * product.num | currency}} 元</strong>
                            </div>
                            <div class="text-right mt-3">
                                <button type="button" class="btn btn-secondary"
                                @click.prevent="getProducts">
                                <i class="fas fa-chevron-circle-left mr-2"></i>返回</button>
                                <button type="button" class="ml-2 btn btn-outline-danger"
                                @click.prevent="addProduct(product.id,product.num)">
                                <i class="fas fa-cart-plus mr-2"></i>加入購物車</button>
                            </div>    
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import $ from 'jquery'
import Cart from '../components/ShoppingCart'
import Pagination from '../components/Pagination';

export default {
    data(){
        return{
            products:[],
            product: {},
            isLoading: false,
            pagination: {},
            status: {
                fileLoading: '',
                cartLoading: ''
            },
            detail: false,
            cartData: {},
            cartLength: "",
            clickCategory: 'all',
        }
    },
    methods:{
        getProducts(page = 1){
            const vm = this;
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products?page=${page}`
            vm.isLoading = true;
            this.$http.get(api).then((response) => {
            vm.isLoading = false;
            vm.detail = false;
            if(response.data.success){
                vm.products = response.data.products;
                vm.pagination = response.data.pagination;
                }
            })
        },
        productDetail(item){
            const vm = this;
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${item.id}`
            vm.status.fileLoading = item.id;
            vm.$http.get(api).then((response) => {
            vm.status.fileLoading = '';
            vm.product = response.data.product;
            vm.$set(this.product, "num", 1);
            vm.detail = true;
            // console.log(vm.product)
            })
        },
        addProduct(id,qty = 1){
            const vm = this;
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
            const cart = {
                product_id : id,
                qty
            };
            vm.status.cartLoading = id;
            vm.$http.post(api,{data:cart}).then((response) => {
                // console.log(response.data)
                vm.status.cartLoading = '';
                $('#productModal').modal('hide')
                vm.getCart();
            })
        },
        getCart(){
            const vm = this;
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
            vm.$http.get(api).then((response) => {
                // console.log(response.data)
                vm.cartData = response.data.data;
                vm.cartLength = response.data.data.carts.length;
            })
        },
        removeCart(id){
            const vm = this;
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`
            vm.$http.delete(api).then((response) => {
                if(response.data.success){
                    vm.getCart();
                }
            })
        },
        assort(name = 'all'){
            const vm = this;
            vm.clickCategory = name;
            vm.detail = false;
        }
    },
    created(){
        this.getProducts();
        this.getCart();
    },
    components: {
        Cart,
        Pagination,
    }
}
</script>

<style lang="scss" scoped>
    .sidebar-active {
        border-right: 4px solid #c0b59a;
        transition: border-right 0.3s linear;
        
    }
    @media (max-width: 992px) { 
        .sidebar-active {
            border-bottom: 4px solid #c0b59a;
            border-right: none;
        }
    }
    
</style>

